<template>
    <div id="createMedia">
        <h3>{{activityOperation}}</h3>
        <div class="addActivity-content">
            <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                <el-form-item label="代理名称" prop="name">
                    <el-input v-model="ruleForm.name" size="small" placeholder="请填写公司名称" style="width:200px;"></el-input>
                </el-form-item>
                <el-form-item label="账号" prop="name">
                    <el-input v-model="ruleForm.name" size="small" placeholder="请填写账号" style="width:200px;"></el-input>
                </el-form-item>
                <el-form-item label="密码" prop="pass" required>
                    <el-input type="password" v-model="ruleForm.pass" placeholder="请输入密码" autocomplete="off" style="width:200px;"></el-input>
                </el-form-item>
                <el-form-item label="充值金额" prop="checkPass" required>
                    <el-input  v-model="ruleForm.checkPass" placeholder="请输入充值金额" autocomplete="off" style="width:200px;"></el-input>
                </el-form-item>
                <el-form-item label="选择媒体" prop="pass" required>
                   <el-select v-model="floorvalue" style="width:200px;"  size="small" placeholder="请选择">
                        <el-option label="全部" value></el-option>
                        <el-option v-for="(item,index) in flooroptions" :key="index" :label="item.floorName"
                                :value="item.floorId"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="submitForm('ruleForm')">立即创建</el-button>

                </el-form-item>
                </el-form>
        </div>
    </div>
</template>

<script>
export default {
    data() {
      return {
        activityOperation:'创建代理',
        imageUrl: '',
        ruleForm: {
          name: '',
          region: '',
          date1: '',
          date2: '',
          delivery: false,
          type: [],
          resource: '',
          desc: '',
          pass:'',
          checkPass:'',
        },
        flooroptions:[
            {
                "floorId":6,
                "mallId":1,
                "floorName":"33FF",
                "floorMain":"卖笑"
            },
            {
                "floorId":37,
                "mallId":1,
                "floorName":"504路",
                "floorMain":"本车从大源北开往同和"
            },
            {
                "floorId":39,
                "mallId":1,
                "floorName":"夜35路",
                "floorMain":"黄鹤路总站（市残运中心）-员村一横路总站"
            },
            {
                "floorId":40,
                "mallId":1,
                "floorName":"三楼",
                "floorMain":"服装"
            },
            {
                "floorId":41,
                "mallId":1,
                "floorName":"四楼",
                "floorMain":"餐饮"
            },
            {
                "floorId":42,
                "mallId":1,
                "floorName":"L5",
                "floorMain":"美妆、食品、床上用品"
            },
            {
                "floorId":44,
                "mallId":1,
                "floorName":"红螺",
                "floorMain":"订单"
            }
        ],
        floorvalue:'',
        rules: {
          name: [
            { required: true, message: '请输入活动名称', trigger: 'blur' },
            { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
          ],
          region: [
            { required: true, message: '请选择活动区域', trigger: 'change' }
          ],
          date1: [
            { type: 'date', required: true, message: '请选择日期', trigger: 'change' }
          ],
          date2: [
            { type: 'date', required: true, message: '请选择时间', trigger: 'change' }
          ],
          type: [
            { type: 'array', required: true, message: '请至少选择一个活动性质', trigger: 'change' }
          ],
          resource: [
            { required: true, message: '请选择活动资源', trigger: 'change' }
          ],
          desc: [
            { required: true, message: '请填写活动形式', trigger: 'blur' }
          ]
        }
      };
    },
    methods: {
        submitForm(formName) {
            this.$refs[formName].validate((valid) => {
            if (valid) {
                alert('submit!');
            } else {
                console.log('error submit!!');
                return false;
            }
            });
        },
        resetForm(formName) {
            this.$refs[formName].resetFields();
        },
        handleAvatarSuccess(res, file) {
            this.imageUrl = URL.createObjectURL(file.raw);
        },
        beforeAvatarUpload(file) {
            const isJPG = file.type === 'image/jpeg';
            const isLt2M = file.size / 1024 / 1024 < 2;

            if (!isJPG) {
            this.$message.error('上传头像图片只能是 JPG 格式!');
            }
            if (!isLt2M) {
            this.$message.error('上传头像图片大小不能超过 2MB!');
            }
            return isJPG && isLt2M;
        }
    }
  }
</script>

<style lang="less" scoped>
#createMedia {
    width: auto;
    min-width: 1000px;
    h3 {
        height: 35px;
        line-height: 35px;
        border-bottom: 1px solid #f3f3f3;
        text-indent: 15px;
        font-size: 14px;
        color: #333;
        font-weight: normal;
    }
   .addActivity-content {
        width: 100%;
        margin-left: 70px;
        margin-top: 30px;
        .avatar-uploader .el-upload {
            border: 1px dashed #d9d9d9;
            border-radius: 6px;
            cursor: pointer;
            position: relative;
            overflow: hidden;
        }
        .avatar-uploader .el-upload:hover {
            border-color: #409EFF;
        }
        .avatar-uploader-icon {
            font-size: 28px;
            color: #8c939d;
            width: 100px;
            height: 100px;
            line-height: 100px;
            text-align: center;
            border: 1px dashed #d9d9d9;
        }
        .avatar {
            width: 100px;
            height: 100px;
            display: block;
        }
    }
}
</style>